body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.1rem !important;
  transition: background-color var(--trans-dur), color var(--trans-dur);

}

body {
  transition: 
    background-color var(--trans-dur) var(--trans-timing),
    color var(--trans-dur) var(--trans-timing);
}

// Common elements transitions
p, h1, h2, h3, h4, h5, h6, span, div {
  transition: 
    color var(--trans-dur) var(--trans-timing),
    background-color var(--trans-dur) var(--trans-timing);
}

// Container elements transitions
.navbar-fixed-top,
.vertical-timeline-element-content,
.expertise-container,
.projects-container,
.contact-container {
  transition: 
    background-color var(--trans-dur) var(--trans-timing),
    border-color var(--trans-dur) var(--trans-timing),
    box-shadow var(--trans-dur) var(--trans-timing);
}

// Interactive elements transitions
button,
.MuiButton-root,
.MuiIconButton-root,
.tech-chip {
  transition: 
    background-color var(--trans-dur) var(--trans-timing),
    color var(--trans-dur) var(--trans-timing),
    border-color var(--trans-dur) var(--trans-timing),
    box-shadow var(--trans-dur) var(--trans-timing),
    transform 0.2s ease !important;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace !important;
  font-size: 1rem !important;
}

.main-container {
  position: relative;
  background-color: #0d1116;
}

a {
  text-decoration: none;
}

@media (min-width: 769px) {
  a:hover {
    color: #5000ca;
    transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out,
      border-color 0.15s ease-in-out;
  }
}

p,
a,
h1,
h2,
h3,
h4,
h5,
label,
button {
  color: white;
  font-family: "Lato", sans-serif;
}

.navigation-bar {
  
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #0d1116;

  button {
    font-size: 1.1rem;
    font-family: "Lato", sans-serif;
    text-transform: initial;
    padding: 6px 10px;
  }

  svg {
    cursor: pointer;
  }
}

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.header-image {
  width: 100%;
  height: 450px;

  img {
    width: 100%;
    height: 450px;
    object-fit: cover;
  }
}

.mobile-menu-top {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 5px;
  font-weight: bold;
}

.items-container {
  padding: 5% 10%;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.light-mode {
  background-color: #f8f9fa;

  .navigation-bar {
    background-color: #f8f9fa;
  }

  p,
  a,
  h1,
  h2,
  h3,
  h4,
  h5,
  label,
  button {
    color: #0d1116;
  }

  .navigation-bar svg {
    color: #0d1116;
  }

  .about-section {
    background-image: url("./assets/images/home/bg-light.jpg");
  }

  .skills-container svg {
    color: #0d1116;
  }

  .chip {
    background-color: rgba(0, 0, 0, 0.08);
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  }

  .vertical-timeline::before {
    background-color: rgba(0, 0, 0, 0.08);
  }

  .vertical-timeline--animate .vertical-timeline-element-content.bounce-in {
    box-shadow: 0px 3px 1px -2px rgba(0, 0, 0, 0.2),
      0px 2px 2px 0px rgba(0, 0, 0, 0.14), 0px 1px 5px 0px rgba(0, 0, 0, 0.12);
  }

  .vertical-timeline--two-columns
    .vertical-timeline-element-content
    .vertical-timeline-element-date {
    color: gray;
  }

  .vertical-timeline-element-content-arrow {
    border-right: 7px solid rgba(0, 0, 0, 0.08) !important;
  }

  .flex-chips .chip-title {
    color: #0d1116;
  }
}

@media screen {
  @media (max-width: 768px) {
    .items-container {
      padding: 5% 5%;
    }
  }
}
